@use '../globals/colors' as *;

.body {
    position: relative;
}

header {
    display: flex;
    position: sticky;
    top: 0;
    align-items: center;
    background-color: rgba(0 0 0 / 0.5);
    color: hsl(0, 0%, 85%);
    z-index: 500;
    line-height: 1;
    font-weight: 300;
    font-size: clamp(1rem, 2vw, 1.5rem);
    padding-inline: 3vw;
    box-shadow: 0px 10px 19px -8px rgba(0,0,0,0.75);
    transition: 250ms ease-in;


    .main-nav {
        flex-grow: 1;
    }

    .nav__list {
        list-style: none;
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: space-between;
        
        .nav__link {
            flex: 1;
        }
    }


    

    .nav__link {
            text-decoration: none;
            display: inline-block;
            font-weight: 400;
            color: inherit;
            padding: 2rem 0;
            text-align: center;
            
            align-self: center;
            transition: 250ms;
        

            &:hover {
                background-color: var(--accent-color);
                color: var(--prim-color);
            }
    }
    
     
}

#phone-logo {
    display: none;
}

#logo-name {
    display: none;

    flex-shrink: 0;
    margin-right: 1em;
    text-align: center;
    font-size: 2rem;
    font-weight: 800;
    
    line-height: 1;
    
    a {
        text-decoration: none;
        color: var(--accent-color);
    }
    
    span {
        color: black;
        font-size: 0.75rem;
        font-weight: 300;
        display: block;
    }
}

.nav-toggle {
    display: none;
    position: relative;
    height: 2rem;
    width: 2rem;
    position: fixed;
    // background: red;
    background: transparent;
    padding: 0.75em;
    cursor: pointer;
    border: 0;
    z-index: 999;
    right: 1.5rem;
    top: 1.5rem;
    
}

.hamb {
    --1: #d9d9d9;
    --2: #3b3b3b;

    display: block;
    position: absolute;
    left: 0;
    right: 0;
    height: 2px;
    background: var(--1);


    &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 8px;
        right: 0;
        height: 2px;
        background: inherit;
        transition: transform 250ms ease-in;
        transform-origin: left;


    }

    &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        height: 2px;
        background: inherit;
        bottom: 8px;
        transition: transform 250ms ease-in;
        transform-origin: right;

    }
}



  .nav-open .hamb::after {
    transform: scaleX(0);
  }

  .nav-open .hamb::before {
    transform: scaleX(0);

  }






@media (max-width: 60em) {

    .nav-toggle {
        display: block;
    }


    #logo-name {
        opacity: 0;
        transform: translateY(300%);
    }
    header {
        display: block;
        position: relative;

        .nav__list {
            flex-direction: column;
            background-color: var(--prim-color);
            color: var(--second-color);
            position: fixed;
            top: 5rem;
            bottom: 0;
            left: 0;
            right: 0;
            transform: translateX(100%);
            transition: transform 350ms ease-in;
            z-index: 20;
        }

        

        .nav__link {
            width: 100%;
            font-weight: 400;
            font-size: 1.2rem;
            padding: 3rem;
            margin: auto;
        }

        .nav__list-item {
            align-items: center;
        }
    }

    .nav-open .nav__list {
        transform: translateX(0);
    }

    

    #phone-logo {
        display: block;
        background-color: var(--prim-color);
        flex-shrink: 0;
        text-align: center;
        font-size: 2rem;
        font-weight: 800;
        line-height: 1;
        height: 5rem;
        position: fixed;
        padding-top: 1rem;
        top: 0;
        left: 0;
        right: 0;
        transform: translateY(-100%);
        transition: transform 250ms ease-in;
        z-index: 21;

            a {
            // width: fit-content;
            text-decoration: none;
            color: var(--accent-color);
             }
            
            span {
                color: black;
                font-size: 0.75rem;
                font-weight: 300;
                display: block;
            }
    }

    
}